import { Component, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { StoreAbstract } from '../../core/classes/store.abstract';
import { IExpansionPanelBoxData } from '../../core/store/expansion-panel-box/expansion-panel-box.reducer';
import { ExpansionPanelComponent } from '../chatflow/components/atom/expansion-panel/expansion-panel.component';
@Component({
  selector: 'app-history',
  templateUrl: './history.component.html',
  styleUrl: './history.component.scss'
})
export class HistoryComponent extends StoreAbstract implements OnInit {

  @ViewChild('container', { read: ViewContainerRef, static: true }) container!: ViewContainerRef;

  ngOnInit() {
    const historyList = localStorage.getItem('history');
    
    let historyObj;
    if (historyList) historyObj = JSON.parse(historyList); 
    
    historyObj.forEach((component: IExpansionPanelBoxData) => {
      this.factoryService.createComponent(
        ExpansionPanelComponent,
        this.container,
        component
      );
    });
  }

}
